<template>
  <fragment>
    <LoadingOverlay v-if="loading"/>

    <DescriptionAndPoliticsSettingsForm
        :loading="loading"
        v-model="localPoliticsDescription"
        @onSave="save"
    />
  </fragment>
</template>

<script>
import {Fragment} from "vue-fragment";
import LoadingOverlay from "@/components/Loading/LoadingOverlay.vue";
import DescriptionAndPoliticsSettingsForm from "@/components/DescriptionAndPoliticsSettingsForm.vue";

export default {
  components: {
    Fragment, LoadingOverlay, DescriptionAndPoliticsSettingsForm
  },

  data: () => ({
    loading: true,
    localPoliticsDescription: {}
  }),

  methods: {
    async save() {
      this.loading = true;
      await this.$store.dispatch("putDescriptionAndPolitics", this.localPoliticsDescription);
      this.loading = false;
    }
  },

  async mounted() {
    document.title = this.$t("Настройки описания и политик бота");
    await this.$store.dispatch("getDescriptionAndPolitics");
    this.localPoliticsDescription = JSON.parse(JSON.stringify(this.$store.getters.descriptionAndPolitics));
    this.loading = false;
  }
}
</script>