<template>
  <v-card>
    <v-card-title>
      <T>Настройки / Описание и политики</T>
    </v-card-title>

    <v-skeleton-loader v-if="loading" type="list-item-three-line"/>

    <v-form v-else v-model="formIsValid" @submit.prevent="$emit('onSave')">
      <v-card-text>
        <ExpansionPanels v-model="openedPanels">
          <Panel :header="$t('Описание бота. Приветственное сообщение, которое видит гость')">
            <v-col cols="12">
              <Editor v-model="model.description"/>
            </v-col>
          </Panel>

          <Panel :header="$t('Политика обработки персональных данных')">
            <v-col cols="12">
              <SwitchField
                  :label="$t('Требовать принятие политики обработки персональных данных гостем')"
                  :hint="$t('Показывать политику обработки персональных данных в боте')"
                  v-model="model.personal_information.show"
                  :rules="model.personal_information.text ? [] : [
                      value => !value || $t('Поле с текстом политики должно быть заполнено')
                  ]"
              />
            </v-col>

            <v-col cols="12">
              <Editor v-model="model.personal_information.text"/>
            </v-col>
          </Panel>

          <Panel :header="$t('Политика конфиденциальности')">
            <v-col cols="12">
              <SwitchField
                  :label="$t('Требовать принятие политики конфиденциальности гостем')"
                  :hint="$t('Показывать политику конфиденциальности в боте')"
                  v-model="model.confidentiality.show"
                  :rules="model.confidentiality.text ? [] : [
                      value => !value || $t('Поле с текстом политики должно быть заполнено')
                  ]"
              />
            </v-col>

            <v-col cols="12">
              <Editor v-model="model.confidentiality.text"/>
            </v-col>
          </Panel>
        </ExpansionPanels>
      </v-card-text>

      <v-card-actions>
        <v-spacer/>
        <v-btn type="submit" color="primary" :disabled="!formIsValid">
          <T>Сохранить</T>
        </v-btn>
      </v-card-actions>
    </v-form>
  </v-card>
</template>

<script>
import ExpansionPanels from "@/components/ExpansionPanels";
import Panel from "@/components/ExpansionPanels/Panel.vue";
import SwitchField from "@/components/ExpansionPanels/SwitchField.vue";
import Editor from "@/components/ExpansionPanels/Editor.vue";

export default {
  components: {
    ExpansionPanels, Panel, SwitchField, Editor
  },

  props: ["value", "loading"],

  emits: ["onSave", "input"],

  data: () => ({
    openedPanels: [0, 1, 2],
    formIsValid: undefined
  }),

  computed: {
    model: {
      get() {
        return this.value;
      },
      set(v) {
        this.$emit("input", v);
      }
    }
  }
}
</script>